import React from 'react';
import Home from './Home';
import Section from '../../components/Section';
import Layout from '../../components/Layout';

async function action({ fetch }) {
  const resp = await fetch('/graphql', {
    body: JSON.stringify({
      query: '{news{title,tagline,photos,url}}',
    }),
  });
  const { data } = await resp.json();
  if (!data || !data.news) throw new Error('Failed to load the news feed.');
  return {
    title: 'Подборка лучших экскурсий по Калининграду',
    chunks: ['home'],
    component: (
      <Layout>
        <Section
          html='Верните до 20% стоимости тура, проживания в отеле или санаторной путёвки с 18 марта<br>по 30 июня 2021 г. <a href="https://xn--b1afakdgpzinidi6e.xn--p1ai/" target="blank" rel="noreferrer">Подробные условия</a>'
          title="<h1>Получите кешбэк <mark>20%</mark> от <strong>Ростуризма</strong> при оплате картой <strong>МИР</strong></h1>"
        />
        <Home news={data.news} />
        {/* <Section html="" title="<h1>Смотреть все предложения:</h1>" /> */}
      </Layout>
    ),
  };
}

export default action;
